<script>
  import {
    getAllPayment,
    getConsumeTypeStatusList,
    getIndexData, getRestaurantInfo,
    getTakeawayOrder,
  } from '../../components/Utlis/api'
  import GlobalSettings from '../../components/Settings/GlobalSettings'
  import AddressInfoBlock from '../../components/base/AddressInfoBlock'
  import DishInfoBlock from '../../components/base/DishInfoBlock'
  import Utils from '../../components/Utlis/Utils'
  import http from '../../components/Utlis/http'
  import moment from 'moment'
  import { hillo, IkDataTable } from 'innerken-utils'
  import i18n from '../../i18n'
  import PrinterList from '../../model/PrinterList'

  export default {
    name: 'DashboardDashboard',
    components: {
      DishInfoBlock,
      AddressInfoBlock,
      IkDataTable,
    },
    data () {
      return {
        timer1: null,
        timer2: null,
        items: ['外卖订单', '失败打印单列表', '销售数据统计', '跑堂排行', '本周热销菜品'],
        salesDataChart: {
          options: {
            low: 0, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0,
            },
          },
        },
        servantHeader: [
          {
            text: 'Name',
            value: 'name',
          },
          {
            text: this.$i18n.t('income'),
            value: 'income',
            align: 'right',
          },
          {
            text: this.$i18n.t('billCount'),
            value: 'billCount',
            align: 'right',
          },
        ],
        dishesHeader: [
          {
            text: this.$i18n.t('Ranking'),
            value: 'rank',
          },
          {
            text: this.$i18n.t('image'),
            value: 'photo',
            align: 'center',
          },
          {
            text: this.$i18n.t('name'),
            value: 'name',
            align: 'right',
          },
          {
            text: this.$i18n.t('price'),
            value: 'price',
            align: 'right',
          },
          {
            text: this.$i18n.t('count'),
            value: 'sumCount',
            align: 'right',
          },
        ],
        indexData: {
          sevenDaysFoodDrinkIncome: [],
          thisMonthServantRank: [],
          todayInfo: {
            sumIncome: '0',
          },
          tableUsageStatus: {
            inUse: '0',
          },
          thisMonthAverageTablesRenewRate: 0,
        },
        headers: [
          {
            text: 'Id',
            align: 'start',
            value: 'id',
          },
          {
            text: this.$i18n.t('下单时间'),
            value: 'createTimestamp',
          },
          {
            text: this.$i18n.t('totalPrice'),
            value: 'totalPrice',
          },
          {
            text: this.$i18n.t('payMethod'),
            value: 'payMethod',
          },
          {
            text: this.$i18n.t('consumeTypeStatus'),
            value: 'consumeTypeStatusId',
          },
          {
            text: this.$i18n.t('action'),
            value: 'action',
            sortable: false,
          },
        ],
        model: PrinterList,
        desserts: [],
        payMethods: [],
        printList: [],
        consumeTypeStatusList: [],
        restaurantName: '',
      }
    },

    computed: {
      filter () {
        return i => i.printStatus !== '1'
      },
      servantImgRoot () {
        return this.$store.getters.rootUrl + GlobalSettings.servantRoot
      },
      dishImgRoot () {
        return this.$store.getters.rootUrl + GlobalSettings.dishImageRoot
      },
      salesData () {
        const labels = []
        const foodData = []
        const drinkData = []
        for (const i of this.indexData.sevenDaysFoodDrinkIncome) {
          const food = i.find(d => d.name.toLowerCase() === 'foods')
          const drink = i.find(d => d.name.toLowerCase() === 'drinks')
          labels.push(food.dateDisplay)
          foodData.push(food.income)
          drinkData.push(drink.income)
        }
        return {
          labels: labels.reverse(),
          series: [foodData.reverse(), drinkData.reverse()],
        }
      },
      thisMonthServantRank () {
        return this.indexData.thisMonthServantRank
      },
      todayInfo () {
        return this.indexData.todayInfo
      },
      tableUsageStatus () {
        return this.indexData.tableUsageStatus
      },
      tableRenew () {
        return this.indexData.thisMonthAverageTablesRenewRate
      },
      thisWeekMostPopularDishes () {
        return this.indexData.thisWeekMostPopularDishes
      },
      totalSales () {
        return this.sales.reduce((acc, val) => acc + val.salesInM, 0)
      },
    },
    created () {
      this.onceData()
      this.reloadData()
      // setInterval(this.reloadItem, 30000)
      this.reloadItem()
      // this.timer1 = setInterval(this.reloadData, 30000)
      this.timer2 = setInterval(this.reloadItem, 30000)
      this.$once('hook:beforeDestroy', () => {
        // clearInterval(this.timer1)
      })
    },
    beforeDestroy () {
      clearInterval(this.timer2)
    },
    methods: {
      toZbon () {
        this.$router.push({ path: '/Bills/zbonlist' })
      },
      toAllBills () {
        this.$router.push({ path: '/Bills/history-bills' })
      },
      addDishes () {
        this.$router.push({ path: '/dishes/dishes-manage' })
      },
      addCategories () {
        this.$router.push({ path: '/dishes/labels-manage' })
      },
      dishStatistic () {
        this.$router.push({ path: '/dishes/dish-statistics' })
      },
      dailyBill () {
        this.$router.push({ path: '/Bills/today-bills' })
      },
      statusText: function (item) {
        if (parseInt(item.consumeTypeStatusId) === 1) {
          console.log(['1', '2', '3'].indexOf(item.payMethod))
          if (['1', '2', '3'].indexOf(item.payMethod) !== -1) {
            return 'Unbezahlt'
          } else {
            return 'Bezahlt'
          }
        } else {
          return this.consumeTypeStatusList.find(i => item.consumeTypeStatusId === i.id).name
        }
      },

      acceptOrder (item, time) {
        const addressInfo = JSON.parse(item.rawAddressInfo)
        let timeReal = moment()
        if (addressInfo) {
          if (addressInfo.date && addressInfo.time) {
            timeReal = moment(addressInfo.date + ' ' + addressInfo.time, 'DD.MM.YYYY HH:mm')
          }
        }
        timeReal.add(time, 'm')

        return http.post('Orders.php?op=acceptTakeawayOrder', {
          tableId: item.tableId,
          reason: timeReal.format('DD-MM-YYYY HH:mm'),
        })

          .then(response => {
            console.log(response)
            this.reloadItem()
          })
      },
      rejectOrder (item) {
        hillo.fastSweetAlertRequest(this.$i18n.t('请输入拒绝接单的理由'), 'text', 'Orders.php?op=rejectTakeAwayOrder', 'reason', { tableId: item.tableId }, this.reloadItem)
      },

      goNext (item) {
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.id },
        })
      },
      onceData () {
        getAllPayment().then(res => {
          this.payMethods = res.content
          this.formatItem()
        })
        getConsumeTypeStatusList().then(res => {
          this.consumeTypeStatusList = res.content
        })
        getPrintList().then(res => {
          for (const i of res.content) {
            if (i.printStatus !== '1') {
              this.printList.push(i)
            }
          }
        })
        getRestaurantInfo().then(res => {
          let arr = []
          arr = res.content[0].name.split('<BR>')
          this.restaurantName = arr.join(' ')
          console.log('=====' + this.restaurantName)
        })
      },
      findLangInArray (langArray, target = i18n.locale) {
        return langArray.find((item) => {
          return item.lang.toLowerCase() === target.toLowerCase()
        })
      },
      formatItem () {
        for (const i of this.payMethods) {
          i.name = this.findLangInArray(i.langs).name
        }
      },
      reloadData () {
        getIndexData().then(res => {
          this.indexData = res
        })
      },
      reloadItem () {
        this.loading = true
        getTakeawayOrder().then(res => {
          if (this.desserts.length > 0) {
            if (res.content.length > this.desserts.length) {
              Utils.play()
            }
          }

          this.desserts = res.content
          this.loading = false
        })
      },
      beforeDestroy () {
        clearTimeout(this.timer1)
        clearTimeout(this.timer2)
        this.timer1 = null
        this.timer2 = null
      },
      complete (index) {
        this.list[index] = !this.list[index]
      },
    },
  }
  export const getPrintList = function (id) {
    return http.get('PrintRecord.php?op=getAllRecords', {
      id: id,
    })
  }
</script>

